
'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Heart, Menu } from 'lucide-react';
import React from 'react';

import { cn } from '@/lib/utils';
import { navLinks } from '@/lib/data';
import { Button } from '@/components/ui/button';
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from '@/components/ui/sheet';
import { ThemeToggle } from '@/components/theme-toggle';
import AvailabilityModal from './availability-modal';

export default function Header() {
  const pathname = usePathname();
  const [availabilityOpen, setAvailabilityOpen] = React.useState(false);

  const renderNavLink = (link: (typeof navLinks)[0], isMobile = false) => {
    if (link.id === 'availability') {
      return (
        <button
          key={link.href}
          onClick={() => setAvailabilityOpen(true)}
          className={cn(
            'font-medium transition-colors hover:text-primary',
            isMobile ? 'text-lg' : 'text-sm',
            'text-foreground/60'
          )}
        >
          {link.label}
        </button>
      );
    }
    return (
      <Link
        key={link.href}
        href={link.href}
        className={cn(
          'font-medium transition-colors hover:text-primary',
          isMobile ? 'text-lg' : 'text-sm',
          pathname === link.href ? 'text-primary' : 'text-foreground/60'
        )}
      >
        {link.label}
      </Link>
    );
  };

  return (
    <>
      <header className="sticky top-0 z-50 w-full bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
        <div className="container flex h-28 max-w-screen-2xl items-center">
          <div className="flex items-center flex-1">
            <div className="md:hidden">
              <Sheet>
                <SheetTrigger asChild>
                  <Button variant="ghost" size="icon">
                    <Menu className="h-5 w-5" />
                    <span className="sr-only">Toggle Menu</span>
                  </Button>
                </SheetTrigger>
                <SheetContent side="left" className="w-[300px] sm:w-[400px]">
                  <SheetHeader>
                    <SheetTitle className="sr-only">Mobile Menu</SheetTitle>
                  </SheetHeader>
                  <nav className="flex flex-col gap-6 mt-8">
                    {navLinks.map(link => renderNavLink(link, true))}
                  </nav>
                </SheetContent>
              </Sheet>
            </div>
            <nav className="hidden md:flex items-center gap-6 text-sm">
              {navLinks.slice(0, 2).map(link => renderNavLink(link))}
            </nav>
          </div>

          <div className="flex-1 flex justify-center">
            <Link href="/" className="flex flex-col items-center space-y-1">
              <Heart className="h-8 w-8 text-primary" />
              <span className="font-bold font-headline text-2xl">Rue's Delectables</span>
            </Link>
          </div>

          <div className="flex items-center justify-end gap-2 flex-1">
              <nav className="hidden md:flex items-center gap-6 text-sm">
                  {navLinks.slice(2).map(link => renderNavLink(link))}
              </nav>
            <ThemeToggle />
          </div>
        </div>
      </header>
      <AvailabilityModal isOpen={availabilityOpen} onOpenChange={setAvailabilityOpen} />
    </>
  );
}
